.progress {
  position: relative;
  user-select: none;
  box-sizing: border-box;
}
.progress * {
  user-select: none;
  box-sizing: border-box;
}
.progress + .progress {
  margin-top: 20px;
}

.progress .progress-indecator {
  padding-bottom: 7px;
  position: relative;
  margin: 5px 0;
  font-family: Montserrat, sans-serif;
  font-size: 15px;
  line-height: 15px;
  font-weight: 400;
  color: #2e2e2e;
}

.progress .progress-title {
  z-index: 100;
  color: var(--clr-white);
  font-family: var(--font-roboto);
  font-size: 15px;
  font-weight: 400;
}

.progress .progress-indecator-inner {
  font-family: var(--progress-font, "Open Sans", sans-serif);
  font-weight: var(--progress-fw, 500);
  font-size: var(--progress-fs, 16px);
  line-height: 1;
  padding: 8px 8px 7px;
  border-radius: 3px;
  color: var(--progress-indecator-color, #fff);
  margin-bottom: 4px;
  background: var(--progress-indecator-bg, #5f5f5f);
  position: absolute;
  bottom: 0;
  transform: translateX(-50%);
}

.progress .down-arrow {
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 7px solid var(--progress-indecator-bg, #5f5f5f);
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translateX(-50%);
}

.progress .progress-bar-wrap {
  height: var(--progressbar-height, 6px);
  background: var(--progressbar-bg, #f0f0f0);
  border-radius: var(--progressbar-raidus, 6px);
  border: 1px solid var(--progressbar-border, transparent);
}

.progress .progress-bar {
  height: var(--progressbar-height, 6px);
  background: var(--progressbar-fill, linear-gradient(to right, #b464ff 0%, #3c64eb 100%));
  width: 0%;
  border-radius: var(--progressbar-raidus, 6px);
}

.progress-inline {
  --progressbar-height: 35px;
  --progressbar-bg: transparent;
  --progressbar-border: #e6e6e6;
  --progressbar-fill: linear-gradient(to right, var(--clr-blue) 0%, var(--clr-light-green) 100%);
  --progressbar-raidus: 3px;
  --progress-font: var(--font-roboto);
  --progress-fw: var(--font-700);
  --progress-fs: var(--fs-13);
  --progress-indecator-color: var(var(--clr-white));
  --progress-indecator-bg: transparent;
}
.progress-inline .progress-title {
  position: absolute;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
  padding: 0;
  margin: 0;
}
.progress-inline .progress-indecator {
  margin: 0;
  padding: 0;
}
.progress-inline .progress-indecator-inner {
  bottom: -36px;
  transform: translateX(-100%);
}
.progress-inline .progress-bar {
  box-shadow: 3px, 5px, 8px, rgba(0, 0, 0, 0.15);
}